div .head {
  background-color: rgba(7,21,30,0.9);
  padding: 1px;
  padding-left: 20px;
  border-bottom: 2px solid #2b353c;
}
div .main {
  display: flex;
}

.form {
  font-size: 20px;
  position: fixed;
  top: 4px;
  right: 4px;
  padding: 5px;
  margin: 3px;
}

.nation {
  color: #fff;
  background-color: #555;
  padding: 3px;
  margin: 12px;
}
.type {
  color: #fff;
