body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

div .head {
  background-color: rgba(7,21,30,0.9);
  padding: 1px;
  padding-left: 20px;
  border-bottom: 2px solid #2b353c;
}
div .main {
  display: -webkit-flex;
  display: flex;
}

.form {
  font-size: 20px;
  position: fixed;
  top: 4px;
  right: 4px;
  padding: 5px;
  margin: 3px;
}

.nation {
  color: #fff;
  background-color: #555;
  padding: 3px;
  margin: 12px;
}
.type {
  color: #fff;
  background-color: #555;
  padding: 3px;
  margin: 12px;
}
.submit {
  color: #fff;
  background-color: #555;
  padding: 3px 10px 3px 10px;
  margin: 14px;
}
body {
  background-color: #0a161f;
}
h1 {
  color: #fff;
}

h2 {
  color: #fff;
}

p {
  color: #cccccc;
}

form {
  color: #fff;
  margin-top: 3px;
  font-weight: bold;
}

li {
  display: block;
  background-color: rgba(7,21,30,0.9);
  padding: 0 0 10px 25px;
  border: 1px solid #2b353c;
}
img {
  padding-left: 10px;
}

.copyright {
  padding: 1px;
  border-top: 2px solid #2b353c;
  background-color: rgba(7,21,30,0.9);
  padding-left: 20px;
}

